.main {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
}

.img_wraper {
  display: block;
  overflow: hidden;
  width: 150px;
  height: 150px;
}

.img_wraper img {
  display: block;
  width: 100%;
  height: 100%;
}
